
import type { StateTree, StoreDefinition, _ActionsTree, _GettersTree } from "pinia";
import type { OidcClientSettings, User } from "oidc-client-ts";
import type {StoreOidcListeners, StoreOidcSettings, OidcStoreState, OidcStoreGetters, OidcStoreMutations, OidcStoreActions } from "./vue-store";

  export interface PiniaOidcStoreState extends StateTree, OidcStoreState{
    [key: string]: string | boolean | string[] | null | undefined | any
  }
  export interface PiniaOidcStoreGetters extends OidcStoreGetters, _GettersTree<PiniaOidcStoreState>{}

  export interface PiniaOidcStoreAction extends OidcStoreActions, OidcStoreMutations, _ActionsTree {}

  export type PiniaOidcStore = PiniaOidcStoreState & PiniaOidcStoreGetters & PiniaOidcStoreAction

  export type PiniaOidcCreateStore = (
    settings: OidcClientSettings,
    storeSettings?: StoreOidcSettings,
    listeners?: StoreOidcListeners
  ) => StoreDefinition<string, PiniaOidcStoreState, PiniaOidcStoreGetters, PiniaOidcStoreAction>;

  export type PiniaOidcStoreDefinition = StoreDefinition<"auth", PiniaOidcStoreState, PiniaOidcStoreGetters, PiniaOidcStoreAction>